import React, { Component } from 'react';
import Modal from 'react-modal';
import Colors from './components/Colors'
import DetailsThumb from './components/DetailsThumb';
import './App.css';
import Image1 from './assets/image/7b719b33aa5d5e03074c.jpg';
import Image2 from './assets/image/26850c243d4ac914905b.jpg';
import Image3 from './assets/image/943560b751d9a587fcc8.jpg';
import Image4 from './assets/image/d028ea4edb202f7e7631.jpg';
import Video1 from './assets/video/bo-thai-doc-caruso-quick-clean.mp4';

class App extends Component {
  state = {
    modalIsOpen: false,
    products: [
      {
        "_id": "1",
        "title": "Thải Độc & Làm Sạch Nội Tạng 15 Ngày Carusos Natural Health Quick Cleanse 15 Day Detox Program",
        "src": [
            Image1,
            Image1,
            Image2,
            Image3,
            Image4
          ],
        "description": "UI/UX designing, html css tutorials",
        "content": "Welcome to our channel Dev AT. Here you can learn web designing, UI/UX designing, html css tutorials, css animations and css effects, javascript and jquery tutorials and related so on.",
        "price": 1680000,
        "discount": 2420000,
        "colors":["red","black","crimson","teal"],
        "count": 1
      }
    ],
    index: 0,
    email: 'nguyenquangha0209@gmail.com',
    feedback: 'Test', 
    name: 'HaNguyenQuang',
  };

  myRef = React.createRef();

  handleTab = index =>{
    this.setState({index: index})
    const images = this.myRef.current.children;
    for(let i=0; i<images.length; i++){
      images[i].className = images[i].className.replace("active", "");
    }
    images[index].className = "active";
  };

  componentDidMount(){
    const {index} = this.state;
    this.myRef.current.children[index].className = "active";
  }

  openModal = () => {
    this.setState({ modalIsOpen: true });
  };

  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };

  handleSubmit = (event) => {
    window.emailjs.send(
      "service_c4hnuvu", "template_vmilezr",
      {
        from_name: "HaNguyenQuang",
        to_name: "HaiNguyenQuang",
        message: "Testtt",
      },
      "user_H6ukGjWJ9Rx5YGWttv3Ew",
      ).then(res => {
        console.log('Email successfully sent!')
      })
      // Handle errors here however you like, or use a React error boundary
      .catch(err => console.error('Oh well, you failed. Here some thoughts on the error that occured:', err))
  }

  handeChangeName = (name) => {
    this.setState({name: name});
  }

  handeChangeEmail = (email) => {
    this.setState({email: email});
  }

  handeChangePhoneNumber = (phoneNumber) => {
    this.setState({name: phoneNumber});
  }

  render() {
    const {products, index} = this.state;
    return (
      <div className="app">
        {
          products.map(item =>(
            <div className="details" key={item._id}>
              {
                this.state.index === 0 ? 
                (
                  <video className="video1" width="700" height="700" controls>
                    <source src={Video1} type="video/mp4" />
                  </video>
                ) : (
                  <div className="big-img">
                    <img src={item.src[index]} alt=""/>
                  </div>
                )
              }
              <div className="box">
                <div className="row">
                  <h2>{item.title}</h2>
                  <div>
                    <span className="discount">₫{item.discount.toLocaleString()}</span>
                    <span className="price">₫{item.price.toLocaleString()}</span>
                  </div>
                  
                </div>
                <Colors colors={item.colors} />

                <p>{item.description}</p>
                <p>{item.content}</p>
                {
                  this.state.index === 0 ? 
                  (
                    <video className="video2" width="700" height="700" controls>
                      <source src={Video1} type="video/mp4" />
                    </video>
                  ) : (
                    <div className="big-img2">
                      <img src={item.src[index]} alt=""/>
                    </div>
                  )
                }
                <DetailsThumb images={item.src} tab={this.handleTab} myRef={this.myRef} />                
                <button onClick={this.openModal} className="cart">Add to cart</button>
              </div>
            </div>
          ))
        }
        <Modal isOpen={this.state.modalIsOpen} onRequestClose={this.closeModal}>
          <div>
            <div>
              <div>Name</div>
              <input type="text" name="user_name" onChange={(e) => this.handeChangeName(e.target.value)}/>
            </div>
            <div>
              <div>Email</div>
              <input type="text" name="email" onChange={(e) => this.handeChangeEmail(e.target.value)}/>
            </div>
            <div>
              <div>Phone Number</div>
              <input type="text" name="phone_number" onChange={(e) => this.handeChangePhoneNumber(e.target.value)}/>
            </div>
          </div>
          <div>
            <button onClick={this.closeModal}>close</button>
            <button onClick={this.handleSubmit}>submit</button>
          </div>
        </Modal>
      </div>
    );
  }
}

export default App;
